.cards {
    max-width: 200px;
    max-height: 200px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.card {
    position: relative;
    width: 220px;
    height: 220px;
    padding: 10px;
    background-color: gray;
    align-self: center;
    justify-self: center;
    place-self: center;
    margin: 0px;
}

.card-blurb {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width:100%;
    color: rgb(211, 81, 125);
}