@import url(https://fonts.googleapis.com/css?family=Roboto:400,300,600,400italic);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
}

.contactform {
  width: 100%;
  margin: 0;
  position: relative;
  display: flex;
  justify-content: center;
}

#contact_form input[type="text"],
#contact_form input[type="email"],
#contact_form input[type="tel"],
#contact_form input[type="url"],
#contact_form textarea,
#contact_form button[type="submit"] {
  font: 400 12px/16px "Roboto", Helvetica, Arial, sans-serif;
  color: black;
}

#contact_form {
  background: #F9F9F9;
  padding: 25px;
  margin: 0;
  max-width: 600px;
  width: 100%;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}

#contact_form .form_top {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

#contact_form .form_left, 
#contact_form .form_right {
  width: 49%;
  min-width: 250px;
}


#contact_form input[type="text"],
#contact_form input[type="email"],
#contact_form input[type="tel"],
#contact_form input[type="url"],
#contact_form textarea {
  width: 100%;
  border: 1px solid #ccc;
  background: #FFF;
  margin: 0 0 5px;
  padding: 10px;
}

#contact_form input[type="text"]:hover,
#contact_form input[type="email"]:hover,
#contact_form input[type="tel"]:hover,
#contact_form input[type="url"]:hover,
#contact_form textarea:hover {
  -webkit-transition: border-color 0.3s ease-in-out;
  -moz-transition: border-color 0.3s ease-in-out;
  transition: border-color 0.3s ease-in-out;
  border: 1px solid #aaa;
}

#contact_form textarea {
  max-width: 100%;
  height: 96%;
  margin-botom: 5px;
  resize: none;
}

#contact_form input:focus,
#contact_form textarea:focus {
  outline: 0;
  border: 1px solid #aaa;
}

#contact_form .not-allowed {
  cursor: not-allowed !important;
}

#contact_form #form_warning {
  color: rgb(250, 116, 116);
  font-weight: bold;
  margin-bottom: -10px;
}

::-webkit-input-placeholder {
  color: #888;
}

:-moz-placeholder {
  color: #888;
}

::-moz-placeholder {
  color: #888;
}

:-ms-input-placeholder {
  color: #888;
}