@import url(https://fonts.googleapis.com/css?family=Roboto:400,300,600,400italic);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.cards {
    max-width: 200px;
    max-height: 200px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.card {
    position: relative;
    width: 220px;
    height: 220px;
    padding: 10px;
    background-color: gray;
    align-self: center;
    justify-self: center;
    place-self: center;
    margin: 0px;
}

.card-blurb {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width:100%;
    color: rgb(211, 81, 125);
}
.projectlist {
    display: grid;
    text-align: center;
    width: 100%;
    grid-template-columns: repeat(auto-fill, 220px);
    grid-gap: 10px;
    justify-items: center;
    align-items: center;
    align-content: center;
    justify-content: center;
    padding: 0px;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
}

.contactform {
  width: 100%;
  margin: 0;
  position: relative;
  display: flex;
  justify-content: center;
}

#contact_form input[type="text"],
#contact_form input[type="email"],
#contact_form input[type="tel"],
#contact_form input[type="url"],
#contact_form textarea,
#contact_form button[type="submit"] {
  font: 400 12px/16px "Roboto", Helvetica, Arial, sans-serif;
  color: black;
}

#contact_form {
  background: #F9F9F9;
  padding: 25px;
  margin: 0;
  max-width: 600px;
  width: 100%;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}

#contact_form .form_top {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

#contact_form .form_left, 
#contact_form .form_right {
  width: 49%;
  min-width: 250px;
}


#contact_form input[type="text"],
#contact_form input[type="email"],
#contact_form input[type="tel"],
#contact_form input[type="url"],
#contact_form textarea {
  width: 100%;
  border: 1px solid #ccc;
  background: #FFF;
  margin: 0 0 5px;
  padding: 10px;
}

#contact_form input[type="text"]:hover,
#contact_form input[type="email"]:hover,
#contact_form input[type="tel"]:hover,
#contact_form input[type="url"]:hover,
#contact_form textarea:hover {
  -webkit-transition: border-color 0.3s ease-in-out;
  transition: border-color 0.3s ease-in-out;
  border: 1px solid #aaa;
}

#contact_form textarea {
  max-width: 100%;
  height: 96%;
  margin-botom: 5px;
  resize: none;
}

#contact_form input:focus,
#contact_form textarea:focus {
  outline: 0;
  border: 1px solid #aaa;
}

#contact_form .not-allowed {
  cursor: not-allowed !important;
}

#contact_form #form_warning {
  color: rgb(250, 116, 116);
  font-weight: bold;
  margin-bottom: -10px;
}

::-webkit-input-placeholder {
  color: #888;
}

:-moz-placeholder {
  color: #888;
}

::-moz-placeholder {
  color: #888;
}

:-ms-input-placeholder {
  color: #888;
}
.unsplashlink {
  /* background-color:black; */
  color:white;
  text-decoration:none;
  padding:1px 1px 0px 1px;
  font-family:-apple-system, BlinkMacSystemFont, "San Francisco", "Helvetica Neue", Helvetica, Ubuntu, Roboto, Noto, "Segoe UI", Arial, sans-serif;
  font-size:12px;
  font-weight:bold;
  line-height:1.2;
  display:inline-block;
  border-radius:3px
}

.unsplashspan {
  display:inline-block;
  padding:1px 1px 0px 1px;
  color: white;
}

.unsplashsvg {
  height:12px;
  width:auto;
  position:relative;
  vertical-align:middle;
  top:-2px;fill:white;
}
.screenshot-container {
    display: "flex";
    justify-content: center;
    width: 100%;
    height: 200px;
}

.tech-list {
    margin-bottom: 20px;
}
